import React, { useContext } from 'react'
import { ResolutionContext } from '../../Context/ResolutionContext'

const SelectResolution = () => {

  const [res, setRes] = useContext(ResolutionContext)

  const handleChange = (e) => {
    setRes(parseInt(e.target.value), 10)
  }

  return (
    <div>
      <select onChange={handleChange}>
        <option value="70">70 x 70</option>
        <option value="60" selected>60 x 60</option>
        <option value="40">40 x 40</option>
        <option value="30">30 x 30</option>
      </select>
    </div>
  )
}

export default SelectResolution
